import './App.css'
import ProjectsByState from './projects'
import React, { useState, useEffect } from 'react'
import serverInterface from './modules/serverInterface'
import project from './models/project'
import myFace from './me.jpg'
import Feedback from './modules/feedback'
import HiddenDetails from './modules/hiddenDetails'
import cvInfo from './information/cvInfo'


const App=()=> {
    const [allProjects, setAllProjects] = useState('loading')
    const [showDetails, setShowDetails] = useState([])
    const [newFeedback, setNewFeedback]= useState('')
    const [newName, setNewName] = useState('')
    const appStates = {allProjects:allProjects,setAllProjects:setAllProjects,
        showDetails:showDetails,setShowDetails:setShowDetails,
        newFeedback:newFeedback, setNewFeedback:setNewFeedback,
        newName:newName, setNewName:setNewName
    }

    useEffect(()=>{

        serverInterface.getAll({collectionModel:project}).then((value)=>{
            console.log(`got ${value}`)
            setAllProjects(value.data)
        }
        )
    },[])
  return (
    <div>
    <div>
        <div>
        <h2>Portfolio Aaro-Antero Kurki
        </h2>
        <HiddenDetails appStates={appStates} info={cvInfo.portfolioInfo}/>
        </div>
        <div>I am motivated 1st year student at TAMK, planning to become a solid professional in the field of programming.</div>
        <h3>ready</h3>
        <ProjectsByState state='ready' appStates = {appStates}/>
        <h3>ongoing</h3>
        <ProjectsByState state='ongoing' appStates = {appStates}/>
        <h3>Planned</h3>
        <ProjectsByState state='planned' appStates = {appStates}/>
    </div>
    <h3>Curriculum Vitae</h3>
    <img src={myFace} alt="My face" height="150"/>
    <div>
    <p>
    <HiddenDetails appStates={appStates} info={cvInfo.workExperience}/>
    </p>
    <p>
    <HiddenDetails appStates={appStates} info={cvInfo.languageSkills}/>
    </p>
    <p>
    <HiddenDetails appStates={appStates} info={cvInfo.hobbies}/>
    </p>
    <p>
    <HiddenDetails appStates={appStates} info={cvInfo.contactInformation}/>
    </p>
    </div>
    <Feedback appStates={appStates}/>

    </div>
  )
}

export default App;
